:root {
  font: 120% / 1.5em;
  font-size: calc(90% + 2vmin);
}

body {
  padding: 1rem;
}

p {
  height: 0;
  margin: 0;
  opacity: 0;
  transform: translate3d(0, -3rem, 0);
  transition: all 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275), height 150ms 50ms cubic-bezier(0.77, 0, 0.175, 1);
  white-space: nowrap;

  &:first-child + & {
    font-weight: bold;
  }

  & + & {
    height: 1.5rem;
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
